// GENERAL DOCUMENT STYLES

::placeholder {
    color: #666;
}

::selection {
    //background-color: $accent;
    color: #fff;
}

* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

.hidden {
    display: none;
    position: absolute;
    top: -9999px;
    left: -9999px;
    -webkit-clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
    clip-path: polygon(0px 0px, 0px 0px, 0px 0px, 0px 0px);
}

.btn {
    padding: 16px 45px;

    font-size: 17px;
    letter-spacing: 0.06em;
    color: $pets-card-button-color;
    background-color: $base-pink;

    border: none;
    border-radius: 50px;

    cursor: pointer;
    transition: 0.3s;
}

.btn:hover {
    background-color: $base-pink-light;
    background-size: 0 100%;
}

.btn--transparent {
    background-color: transparent;
    border: 2px solid $base-pink;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.btn--transparent:hover {
    border-color: $base-pink-light;
}

.section__title {
    margin: 0;
    font-size: 35px;
    line-height: 130%;
    font-weight: 400;
    letter-spacing: 0.05em;
    text-align: left;

    color: $main-color;
}

.section__title--center {
    text-align: center;
}

.section__title--white {
    color: $base-white;
}

input, textarea {
    outline: none;
    &:focus:required:invalid { border-color: red }
    &:required:valid { border-color: green }
}

body,
html {
    width: 100%;
    height: 100%;
}

html {
    font-size: $font-size;
}

body {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    min-width: 320px;

    @include Georgia();
    font-size: rem($font-size);
    line-height: rem($line-height);
    -webkit-font-smoothing: antialiased;
    color: $text-color;

    overflow-x: hidden;
}

.wrapper {
    flex: 1 0 auto;
    position: relative;
    width: 100%;

    &_padding-bottom{
        padding-bottom: 100px;
    }
}

.container {
    max-width: $container-width;
    box-sizing: border-box;
    margin: 0 auto;
}

li {
    list-style: none;
}

ul {
    padding: 0;
    margin: 0;
}

img {
    max-width: 100%;
}